.banner__container {
  background: var(--troperial-banner-blue);
  padding: 25px 20px;
  border-radius: 10px;
  border: 1px solid var(--troperial-banner-border);
  box-sizing: border-box;
}
.banner__textContent p,
h3 {
  margin: 0;
  padding: 0;
}
.banner__textContent p {
  color: var(--dark-grey);
  font-weight: medium;
  margin: 10px 0;
}
.banner__textContent h3 {
  font-size: 1.4rem;
  margin: 0 0 10px 0;
}
.banner__listing-btn {
  display: block;
  box-sizing: border-box;
  transition: 0.5s ease-in-out;
  border: none;
  border-radius: 8px;
  outline: none;
  font-size: 1rem;
  color: var(--original-white);
  background: var(--troperial-blue);
  font-weight: medium;
  padding: 10px 15px;
}

.banner__listing-btn:hover {
  cursor: pointer;
  background: rgb(3, 117, 211);
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1),
    0 10px 10px -5px rgba(0, 0, 0, 0.04);
}
.banner__image {
  display: none;
  border-radius: 10px;
}
@media (min-width: 768px) {
  h3 {
    font-size: 0.8rem;
  }
  .banner__image {
    display: inline;
  }
  .banner__image img {
    width: 100%;
    height: 100%;
    border-radius: 10px;
  }
  .banner__container {
    width: 70%;
    display: flex;
    justify-content: space-between;
    padding: 0;
  }
  .banner__textContent {
    color: var(dark-grey);
    padding: 20px 15px;
  }
  .banner__textContent p {
    width: 80%;
    line-height: 1.6;
  }
}

@media (min-width: 1110px) {
  .banner__container {
    width: 55%;
  }
}
